<template>
  <!-- 周计划 -->
  <div class="main_con_box">
    <div class="top_box">
      <div class="top_box_left">
        <el-date-picker
          v-model="plan_date"
          size="mini"
          style="width: 300px"
          type="daterange"
          range-separator="至"
          start-placeholder="计划时段"
          end-placeholder="计划时段"
        >
        </el-date-picker>
        <el-select
          class="top_sel"
          size="mini"
          v-model="plan_empId"
          placeholder="计划人"
        >
          <el-option
            v-for="item in plan_emplist"
            :key="item.value1"
            :label="item.label"
            :value="item.value1"
          >
          </el-option>
        </el-select>

        <el-button
          class="search_btn"
          type="primary"
          size="mini"
          @click="search()"
          >查 询</el-button
        >
        <el-button size="mini" @click="resetForm()">重 置</el-button>
      </div>
      <div>
        <el-button type="primary" size="mini" @click="onSubmit()"
          >新 建</el-button
        >
      </div>
    </div>
    <div class="table_box">
      <el-table
        :data="tableData"
        class="eltable"
        border
        style="width: 100%"
        :height="$store.state.innerheight - 218"
        :header-cell-style="{background:'#f5f5f5'}"
        size="mini"
        :span-method="objectspanmethod"
      >
        <el-table-column prop="plan_date" label="计划时段">
        </el-table-column>
        <el-table-column prop="plan_emp" label="计划人">
        </el-table-column>
        <el-table-column prop="plan_title" label="主题">
        </el-table-column>
        <el-table-column prop="plan_status" label="审核状态">
        </el-table-column>
        <el-table-column prop="plan_business" label="计划业务">
        </el-table-column>
        <el-table-column prop="plan_num" label="计划数量/数额">
        </el-table-column>
        <el-table-column prop="plan_yes" label="已完成">
        </el-table-column>
        <el-table-column prop="plan_rate" label="进度">
          <template #default="scope">
            <el-progress class="elprogress" width="35" type="circle" :percentage="scope.row.plan_rate"></el-progress>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="250">
          <template #default>
            <el-button size="mini" type="text">驳回审批</el-button>
            <el-button size="mini" type="text" @click="handleEdit()">编辑</el-button>
            <el-button size="mini" type="text" style="color:red;">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="footer_box" style="margin-top:5px;">
      <el-pagination
          v-model:currentPage="currentPage4"
          :page-sizes="[20, 50, 70, 100]"
          :page-size="20"
          layout="total, sizes, prev, pager, next, jumper"
          :total="400"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getCurrentInstance, onMounted, reactive, toRefs } from "vue";
export default {
  setup() {
    const { proxy } = getCurrentInstance()
    const state = reactive({
      plan_empId: "",
      plan_emplist: [],
      plan_date: "",
        tableData:[
          {
            code:1,
            plan_date:'2021年-第3季度',
            plan_emp:'王立',
            plan_title:'xxxxx',
            plan_status:'已通过',
            plan_business:'跟进-电话',
            plan_num:'10',
            plan_yes:'5',
            plan_rate:25
          },
          {
            code:1,
            plan_date:'2021年-第3季度',
            plan_emp:'王立',
            plan_title:'xxxxx',
            plan_status:'已通过',
            plan_business:'跟进-拜访',
            plan_num:'10',
            plan_yes:'5',
            plan_rate:60
          },
          {
            code:1,
            plan_date:'2021年-第3季度',
            plan_emp:'王立',
            plan_title:'xxxxx',
            plan_status:'已通过',
            plan_business:'新增意向客户',
            plan_num:'10',
            plan_yes:'5',
            plan_rate:25
          }
        ],
        spanArr:[],
        position:0,
        currentPage4:1
    });
    const onSubmit = () => {
      proxy.$router.push("/planInformation");
    };
    const handleEdit = () => {
      proxy.$router.push("/planInformation");
    };
    const search = () => {
      console.log();
    };
    const resetForm = () => {
      console.log();
    };
    // 合并表格的根据某个id值
    const objectspanmethod = ({row,column,rowIndex,columnIndex}) =>{
      console.log(row)
      console.log(column)
      for(var i = 0; i < 4; i++){
        if(i==columnIndex) {
          const _row = proxy.spanArr[rowIndex]
          const _col = _row > 0 ? 1 : 0
          return {
            rowspan:_row,
            colspan:_col
          }
        }
      }
    }
    // 先把获取到的数据按规定值相同的放一起
    const rowspan = () => {
      proxy.spanArr = []
      proxy.position = 0
      proxy.tableData.forEach((item,index) => {
        if(0 == index) {
          proxy.spanArr.push(1)
          proxy.position = 0
        }else{
          if(proxy.tableData[index].code == proxy.tableData[index - 1].code){
            proxy.spanArr[proxy.position] += 1
            proxy.spanArr.push(0)
          }else{
            proxy.spanArr.push(1)
            proxy.position = index
          }
        }
      })
    }
    const handleSizeChange = (val) => {
        console.log(`${val} items per page`)
    }
    const handleCurrentChange = (val) => {
        console.log(`current page: ${val}`)
    }
    onMounted(()=>{
      proxy.rowspan()
    })
    return {
      ...toRefs(state),
      onSubmit,
      search,
      resetForm,
      objectspanmethod,
      rowspan,
      handleSizeChange,
      handleCurrentChange,
      handleEdit
    };
  },
};
</script>

<style scoped>
.top_box {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  box-sizing: border-box;
}
.top_sel {
  margin-left: 10px;
}
.search_btn {
  margin-left: 10px;
}
.table_box{
  margin-top: 10px;
  padding: 0 10px;
  box-sizing: border-box;
  margin-bottom: 10px;
}
</style>
<style>
    .elprogress .el-progress__text{
      left: -8px;
    }
    .eltable.el-table .cell{
      line-height: inherit;
    }
