<template>
  <div>
      <!-- 审批弹窗 -->
    <el-dialog
      v-model="dialogVisible"
      title="审批"
      width="30%"
    >
      <div class="contain">
        <div class="item_box">
            <small>审批备注</small>
            <div class="data_box">
                <el-input v-model="dataobj.remark" size="mini"></el-input>
            </div>
        </div>
        <div class="item_box">
            <small>审批人</small>
            <div class="data_box selectel">
                <el-select v-model="dataobj.empId" size="mini" filterable clearable>
                    <el-option
                    v-for="item in emplist"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                    >
                    </el-option>
                </el-select>
            </div>
        </div>
        <div class="item_box">
            <small>审批时间</small>
            <div class="data_box pickerel">
                <el-date-picker class="pickerel" v-model="dataobj.date" type="datetime" size="mini">
                </el-date-picker>
            </div>
        </div>
      </div>

      <template #footer>
        <span class="dialog-footer">
          <el-button
            type="primary"
            size="mini"
            >通过</el-button
          >
          <el-button type="danger" size="mini"
            >拒绝</el-button
          >
          <el-button @click="dialogVisible = false" size="mini">取消</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { reactive, toRefs } from "vue";
import { getCurrentInstance } from "@vue/runtime-core";
export default {
  setup() {
    const { proxy } = getCurrentInstance();
    const state = reactive({
      dialogVisible:false,
      dataobj:{},
      emplist:[]
    });
    const changestatus = () => {
        proxy.dialogVisible = true
    }
    return {
      ...toRefs(state),
      changestatus
    };
  },
};
</script>

<style  scoped>
.contain {
  margin: 10px;
}
.item_box{
    display: flex;
    align-items: center;
    margin-top: 10px;
}
.item_box>small{
    width: 100px;
    text-align: right;
    margin-right: 10px;
}
.item_box>div{
    width: calc(100% - 110px);
}
.data_box.selectel .el-select{
    width: 100%;
}

</style>
<style>
.data_box.pickerel .el-date-editor{
    width: 100% !important;
}
</style>